
nav {
  border-bottom: 3px solid gray;
  text-align: center;
  position:relative;
  top: 0;
  width: 100%;
  background: white;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 20px;
}

nav a {
  text-decoration: none;
  color:black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: black;
  color: white;
}


.article-list-item {
  text-decoration: none;
  color: black;
  text-align: center;
  background-color: palegoldenrod;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

  #logo{
    float:left;
    width:4%;
  }

  #button {
    background-color: purple;
    color: white;
    padding: 0px 20px;
    border-radius: 8px;
    text-decoration: none;
    display: inline-block;
    font-size: 15px;
    margin: 4px 2px;
    cursor: pointer;
    height: 30px;
  }
  #main{
    background-image: url('https://cdn.pixabay.com/photo/2016/12/05/19/43/pill-1884775_960_720.jpg');
    height: 750px;
  }
  
  /*About US CSS */
  #aboutglobal{ 
    background-color: antiquewhite;
    height: 800px;
  }
  #aboutbar{
    float:left;
    width: 100%;
    height: 70px;
  }
  .aboutinfo{
    float:left;
    width: 100%;
  }
  #aboutleft{
    float: left;
    padding-left: 70px;
    padding-right: 30px;
    padding-top: 20px;
    text-align: center;
    width: 20%;
  }
  #aboutmid{
    float:left;
    padding-top: 20px;
    padding-left: 20px;
    text-align: center;
    width: 40%;
  }
  #aboutright{
    float:left;
    padding-top: 20px;
    padding-left: 30px;
    text-align: center;
    width: 20%;
  }

  .contactinfo{
    float:left;
    width: 100%;
    background-color: antiquewhite;
  }
  #contactleft{
    float: left;
    padding-left: 300px;
    padding-top: 30px;
  }
  #contactright{
    float:left;
    padding-top: 30px;
    padding-left: 100px;
  }
 
  /* CSS for the Products page */
  #top{
    float:left;
    width: 100%;
  }
  #item-shape{
    padding-left: 200px;
    padding-right: 300px;
    background-color: antiquewhite;
  }
  
#userCommentForm {
  border-bottom: 2px solid black;
  padding: 16px;
}

#userCommentForm h3 {
  margin-bottom: 16px;
}

#userCommentForm input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#userCommentForm button {
  width: 320px;
  background-color: purple;
  color: white;
  padding: 0px 20px;
  border-radius: 8px;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  margin: 4px 2px;
  cursor: pointer;
  height: 30px;
}

.userComment {
  border-bottom: 2px solid black;
  padding: 15px;
}

